import React, { forwardRef } from 'react';
import MenuItem from './MenuItem';
import './styles.css';

const Menu = forwardRef(({ handleScrollMenu, list }, ref) => (
  <main ref={ref} key='test'>
    {list.map((item) => (
      <MenuItem handleScrollMenu={handleScrollMenu} item={item} key={item.id} />
    ))}
  </main>
));

export default Menu;
