import React from 'react';
import { useRef } from 'react';
import { connect } from 'react-redux';
import { createStructuredSelector } from 'reselect';
import ButtonCartCount from '../../components/common/ButtonCartCount';
import Footer from '../../components/common/Footer';
import Banner from '../../components/Home/Banner';
import Menu from '../../components/common/Menu';
import MealPlans from '../../components/common/MealPlans';
import { menuItemsData } from '../../components/common/Menu/data';
import { mealPlanItemsData } from '../../components/common/MealPlans/data';
import { selectCartItems, selectCartItemsCount } from '../../redux/cart/cart.selector';
import './styles.css';

const Home = ({ cartItems, cartCount }) => {
  const mealPlansRef = useRef();
  const menuRef = useRef();

  const scrollMenuIntoView = () => {
    setTimeout(() => {
      window.scrollTo({
        top: 10000,
        behavior: 'smooth'
      })
    }, 100);
  }

  const scrollMealPlansIntoView = () => {
    mealPlansRef.current.scrollIntoView({ behavior: 'smooth' });
  }

  const DisplayMenu = () => {
    const hasAddedMealPlan = cartCount !== 0 && cartItems.find((cartItem) => String(cartItem.id)[0] == 8);
    try {
      if (hasAddedMealPlan) {
        return <Menu list={menuItemsData} ref={menuRef}/>
      } else {
        return null;
      }
    } finally {
      if (hasAddedMealPlan) {
        scrollMenuIntoView()
      }
    }
  }

  return (
    <div>
      <Banner handleScrollMealPlans={scrollMealPlansIntoView} />
      <MealPlans list={mealPlanItemsData} ref={mealPlansRef} />
      <DisplayMenu />
      <Footer />
      <ButtonCartCount />
    </div>
  );
};

const mapStateToProps = createStructuredSelector({
  cartItems: selectCartItems,
  cartCount: selectCartItemsCount,
});

export default connect(mapStateToProps)(Home);
